<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[]
    };
  },
  methods: {
    deletes(item) {
      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
        axios.delete(`${baseUrl}contact/${item.id}/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
              'Accept-Language':'oz'
            }
          })
          .then(() => {
            this.$toast("Ma'lumot o'chirildi", {
              timeout: 2000,
              type:"success"
            });
            axios.delete(`${baseUrl}contact/${item.id}/`,
              {
                headers: {
                  'Authorization': `Bearer ${localStorage.getItem('token')}`,
                  'Accept-Language':'ru'
                }
              })
            axios.delete(`${baseUrl}contact/${item.id}/`,
              {
                headers: {
                  'Authorization': `Bearer ${localStorage.getItem('token')}`,
                  'Accept-Language':'kar'
                }
              })
            axios.get(`${baseUrl}contact/`)
              .then(res=>{
                this.contact=res.data
              })

          })
      }
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
      this.main_id=''
      this.telegram=''
      this.instagram=''
      this.facebook=''
      this.email_address=''
      this.phone_number=''
      this.address=''
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    create(){
      axios.post(`${baseUrl}contact/`,{
          "phone_number":this.phone_number,
          "email_address":this.email_address,
          "address":this.address,
          "facebook":this.facebook,
          "instagram":this.instagram,
          "telegram":this.telegram
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(()=>{
          axios.get(`${baseUrl}contact/`)
            .then(res=>{
              this.contact=res.data
              this.phone_number=''
              this.email_address=''
              this.address=''
              this.facebook=''
              this.instagram=''
              this.telegram=''
              this.$toast("Ma'lumotlar kiritildi", {
                timeout: 2000,
                type:"success"
              });
              axios.get(`${baseUrl}contact/`)
                .then(res=>{
                  this.contact=res.data
                  console.log(res)
                })
            })
        })
    },
    edit(){
      axios.patch(`${baseUrl}contact/${this.main_id}/`,{
          "title":this.title,
          "order":this.order,
          "url":this.url,
          "is_footer":this.isFooter
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(()=>{
          this.$toast("Ma'lumotlar o'zgartirildi", {
            timeout: 2000,
            type:"success"
          });
          axios.get(`${baseUrl}contact/`)
            .then(res=>{
              this.contact=res.data

            })
        })
    },
    edit_main(row){
      this.isButton=true
      this.main_id=row.id
      this.telegram=row.telegram
      this.instagram=row.instagram
      this.facebook=row.facebook
      this.email_address=row.email_address
      this.phone_number=row.phone_number
      this.address=row.address
    }
  },
  mounted() {
    axios.get(`${baseUrl}contact/`)
      .then(res=>{
        this.contact=res.data
        console.log(res)
      })
  }
}

</script>
